#player-health {
  font-family: "Press Start 2P", cursive;
}

#opponent-health {
  font-family: "Press Start 2P", cursive;
}

.attacks {
  justify-content: center; /* horizontal */
  align-items: center; /* vertical */
  margin: 0 auto;
  display: block;
}

.switch {
  background-image: url("../src/images/gameboy/switch.png");
  background-repeat: no-repeat;
  background-size: 1800px;
  background-position: center;
  display: flex;
  justify-content: center;
  background-color: gray;
  height: 625px;
}

.welcome {
  top: 65px;
  color: black;
  text-align: left;
  font-family: "Press Start 2P", cursive;
  line-height: 45px;
  background-color: gray;
  left: 50%;
  transform: translateX(-50%);
  font-size: 30px;
  position: absolute;
  text-align: center;
}

.btn {
  display: block;
  border: none;
  background-color: transparent;
  font-family: "Press Start 2P", cursive;
  position: absolute;
  width: 115px;
  height: 25px;
  font-size: 10.5px;
}

.hp {
  display: inline-block;
}

#social-media {
  color: black;
}

#parent {
  display: flex;
  justify-content: space-around;
}

#left {
  width: 50px;
}

#right {
  width: 20%;
}

.links {
  display: flex;
  justify-content: center;
  padding-top: 5px;
  padding-bottom: 35px;
  background-color: gray;
}

.social-media {
  float: center;
  margin-left: 50px;
  font-size: 50px;
}

.github {
  color: black;
}

.linkedin {
  color: #0e76a8;
}

.health-bar {
  margin-left: 125px;
  z-index: 1 !important;
}

body {
  background-color: gray;
}

.screen {
  padding: 5px;
  margin-top: 230px;
  background-image: url("../src/images/levels/arena-forrest.jpg");
  background-repeat: no-repeat;
  width: 590px;
  border-radius: 10px;
  height: 325px;
  background-size: 100%;
  position: relative;
  margin-right: 5px;
}

.attack-buttons {
  position: absolute;
  bottom: 15px;
  height: 100px;
  left: 5px;
  right: 5px;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
}

#m0 {
  top: 15px;
  left: 300px;
}
#m1 {
  top: 15px;
  left: 425px;
}
#m2 {
  top: 57px;
  left: 300px;
}
#m3 {
  top: 57px;
  left: 425px;
}

.background-buttons {
  border-radius: 10px;
  position: absolute;
}

#commentary {
  position: absolute;
  width: 185px;
  height: 55px;
  top: 25px;
  left: 75px;
  text-align: center;
  font-size: 10px;
  line-height: 20px;
  font-family: "Press Start 2P", cursive;
}

#player {
  position: absolute;
  top: 150px;
  left: 100px;
}

#opponent {
  position: absolute;
  top: 40px;
  left: 420px;
}

#opponent-stats {
  position: absolute;
  top: 50px;
  left: 0;
}
#player-stats {
  position: absolute;
  top: 150px;
  right: 0;
}

.player-hp {
  width: 175px;
}

.opponent-hp {
  width: 190px;
}

#health1 {
  position: absolute;
  width: 73px;
  height: 12px;
  top: 173px;
  right: 6.75px;
}

#health2 {
  position: absolute;
  width: 79px;
  height: 12px;
  top: 75px;
  left: 78px;
}

#instructions {
  line-height: 20px;
  font-size: 12px;
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
  p {
    text-align: center;
    width: 700px;
    border: 1px solid;
    border-radius: 5px;
    background-color: white;
    padding: 15px;
    font-family: "Press Start 2P", cursive;
  }
}

#player-hp-container {
  width: 5px;
  height: 5px;
}

#player-health {
  position: absolute;
  top: 189px;
  right: 6px;
  font-size: 9.5px;
}

#opponent-hp-container {
  width: 5px;
  height: 5px;
}

#opponent-health {
  position: absolute;
  top: 80px;
  left: 10px;
  font-size: 10px;
}

#player-name-container {
  width: 10px;
  height: 10px;
}

#player-name {
  position: absolute;
  top: 158.5px;
  right: 48.5px;
  font-size: 10px;
  font-family: "Press Start 2P", cursive;
  display: flex;
  justify-content: center;
}

#opponent-name-container {
  width: 10px;
  height: 10px;
}

#opponent-name {
  position: absolute;
  top: 61px;
  left: 20px;
  font-size: 10px;
  font-family: "Press Start 2P", cursive;
  display: flex;
  justify-content: center;
}

#sound-toggle {
  background-color: transparent;
  border-color: transparent;
}

#player-level-container {
  width: 10px;
  height: 10px;
  p {
    color: #494949;
    font-weight: lighter;
    display: inline-block;
    transform: scale(0.85, 1.4);
  }
}

#player-level {
  position: absolute;
  top: 159.5px;
  right: 2px;
  font-size: 8px;
  font-family: "Press Start 2P", cursive;
  display: flex;
  justify-content: center;
}

#opponent-level-container {
  width: 10px;
  height: 10px;
  p {
    color: #494949;
    font-weight: lighter;
    display: inline-block;
    transform: scale(1, 1.2);
  }
}

#opponent-level {
  position: absolute;
  top: 61.5px;
  left: 148px;
  font-size: 8.55px;
  font-family: "Press Start 2P", cursive;
  display: flex;
  justify-content: center;
}

#player-slash-container {
  // width: 10px;
  // height: 10px;
}

#player-slash {
  position: absolute;
  top: 105px;
  left: 50px;
  font-size: 10px;
  font-family: "Press Start 2P", cursive;
  display: flex;
  justify-content: center;
  object-fit: cover;
}

#opponent-slash-constainer {
  // width: 10px;
  // height: 10px;
}

#opponent-slash {
  position: absolute;
  top: 30px;
  right: 50px;
  font-size: 10px;
  font-family: "Press Start 2P", cursive;
  display: flex;
  justify-content: center;
  object-fit: cover;
}


